import React, { Component } from 'react';
import styled,{css} from 'styled-components';

const SearchDiv = styled.div`
  align-self : center;
  text-align : center;
  outline : none;
  border : 1px solid #222;
  padding : 1px 6px;
  margin-right : 2px;
  transition : 0.2s border ease-out,0.5s width ease-out;
  display : flex;
  ${props => props.focus && css`
    border : 1px solid rgba(229, 151, 0, 255);
  `}
  `
const SearchInput = styled.input`
  width : 50vw;
  font-size : 1.2em;
  border : none;
  padding : 0 5px;
  background : #222;
  color : #ddd;
  outline : none;
  text-align : center;
  display : none;
  ${props => props.focus && css`
    text-align : left;
    display : block;
`}
  `
const SearchButton = styled.button`
  border : none;
  outline : none;
  background : inherit;
  color : #ddd;
  `
class Search extends Component {
  constructor(props){
    super(props);
    this.state = {
      value : '',
      focus : {},
      searchInput : 'close',
    };
    this._onChange = this._onChange.bind(this);
    this._onSubmit = this._onSubmit.bind(this);
    this._onFocus = this._onFocus.bind(this);
    this._onBlur = this._onBlur.bind(this);
    this._onClick = this._onClick.bind(this);
  }
  _onChange(e){
    this.setState({value : e.target.value})
  }
  _onSubmit(e){
    e.preventDefault();
    this.props._onSubmit(this.state.value);
  }
  _onFocus(){
    this.setState({
      focus : {'focus' : 'focus'},
      searchInput : "search",
    })
  }
  _onBlur(){
    if(this.state.value === "")
      this.setState({
        focus : {},
      })
    this.setState({searchInput : "close"})
  }
  _onClick(inp){
    //console.log(inp);
    this.setState({
      focus : this.state.searchInput === "close" ? {'focus' : 'focus'} : {},
      searchInput : this.state.searchInput === "close" ? "search" : "close"
    })

  }
  render() {
    var inp,searchClass = this.state.searchInput==="search"? "close" : "search";
    return (
        <SearchDiv {...this.state.focus}>
          <form onSubmit={this._onSubmit}>
            <SearchInput type="text"  ref={(node) => inp = node} value={this.state.value} placeholder="Search" onChange={this._onChange} onFocus={this._onFocus} onBlur={this._onBlur}  {...this.state.focus}/>
          </form>
          <SearchButton onClick={e => this._onClick(inp)}><i className={"fa fa-"+ searchClass + " fa-2x"}/></SearchButton>
        </SearchDiv>
        
    );
  }
}

export default Search;



