import React, { Component } from 'react';
import io from 'socket.io-client';
import styled from 'styled-components';
import DisplayResults from './components/DisplayResults';
import Header from './components/Header';
import Controls from './components/Controls';

import './App.css';

const Content = styled.div`
  padding-top : 40px;
  display : flex;
  flex-direction : column;
  margin-bottom : 15px;
  `

const socket = io('http://'+window.location.host);

const youtubeurl  = function(query){
  let apikey = 'AIzaSyClHWfMurGrGg89h46VNVIxTDlUWZ3hQCc';
  let max_videos = '20';
  return 'https://www.googleapis.com/youtube/v3/search?order=viewCount&part=snippet&q='+ escape(query) + "&type=video+&videoDefinition=high&key=" + apikey + "&maxResults="+ max_videos;
}

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      json : null,
      play : "play",
    }
    this._onSubmit = this._onSubmit.bind(this);
    this.sendKey = this.sendKey.bind(this);
  }
  _onSubmit(value){
    this.setState({json : null});
    fetch(youtubeurl(value)).then(res => res.json()).then(json => this.setState({
      json : json
    }));
  }
  sendKey(key){
    socket.emit('key',key);
  }
  sendLink(url,snippet){
    socket.emit('video',url);
  }
  render() {
    const {json,play} = this.state;
    return (
      <div className="App">
        <Header sendKey={this.sendKey} _onSubmit={this._onSubmit}/>
        <Content>
          {
            <DisplayResults sendLink={this.sendLink} {...json}/>
          }
          <div>
            <Controls play={play} sendKey={this.sendKey} />
          </div>
        </Content>
      </div>
    );
  }
}

export default App;



