import React from 'react';
import styled from 'styled-components';

const VideoCardDiv = styled.div`
    padding : 1px 10px;
    padding-bottom : 10px;
    margin : 10px 0;
    border-bottom : 1px solid #555;
    display: flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;
    `
const TitleDiv = styled.div`
    color : #48a3c6;
    margin : 2px 0;
    width : 300px;
    text-align : center;
    `
const CastButton = styled.button`
    margin-top : 10px;
    padding : 0;
    width : 40px;
    height : 40px;
    font-size : 1em;
    border : none;
    border-radius : 50%;
    background-color : #75a441;
    color : white;
    outline : none;
    `
const VideoImg = styled.img`
    border : 1px solid #222;
    width : 300px;
    `

const VideoCard = (props) => {
    const {thumbnails,title} = props.snippet;
    return(
        <VideoCardDiv>
            <VideoImg src={thumbnails.high.url}  alt='thumbnail'/>
            <TitleDiv>{title}</TitleDiv>
            <div>
                <CastButton onClick={() => props.sendLink(props.id.videoId,props.snippet) }>
                    <i className='fa fa-play'/>
                </CastButton>
            </div>
        </VideoCardDiv>
    )
} 

export default VideoCard;


