import React from 'react';
import styled from 'styled-components';

const ControlsDiv = styled.div`
    padding : 5px 0;
    background-color : #222;
    position : fixed;
    bottom : 0px;
    width : 100vw;
    display : flex;
    justify-content: center;
    z-index : 2;
    box-shadow : 0 -5px 2px 0 rgba(0,0,0,0.12);
    `
const ControlButton = styled.button`
    margin-left: 10px;
    margin-right: 10px;
    border : none;
    height : 30px;
    width : 30px;
    border-radius : 50%;
    background-color : #75a441;
    color : white;
    outline : none;
    `

const Controls = (props) => {
    return (
        <ControlsDiv>
            <ControlButton><i className={"fa fa-play"} onClick={e => props.sendKey('p')}/></ControlButton>
            <ControlButton><i className="fa fa-backward" onClick={e => props.sendKey("\u001b[D")}/></ControlButton>
            <ControlButton><i className="fa fa-forward" onClick={e => props.sendKey("\u001b[C")}/></ControlButton>
            <ControlButton><i className="fa fa-stop" onClick={e => props.sendKey('q')}/></ControlButton>
        </ControlsDiv>
    )
}

export default Controls;


