import React,{Component} from 'react';
import logo from '../logo.svg';
import Search from './Search';
import styled from 'styled-components';

const HeaderDiv = styled.div`
    position: fixed;
    top : 0;
    left : 0;
    height : 40px;
    width : 100vw;
    z-index: 2;
    padding : 2px;
    background-color : #222;
    color : #fff;
    box-shadow: 0 5px 2px 0 rgba(0,0,0,0.12);
    display : flex;
    align-items: center;
    justify-content : space-between;
    font-weight: bold;
    font-size : 1.5em;`

class Header extends Component{
    constructor(props){
        super(props);
        this.state = {
            showSearch :false,
        };
    }
    render(){
        return(
            <HeaderDiv>
                <label><img src={logo} height='25px' alt='pi'/>PiCast</label>
                <Search _onSubmit={this.props._onSubmit}/>
            </HeaderDiv>
        )
    }
}

export default Header;


